<script>
export default {
  name: "App",
  data() {
    return {
      h1: "",
      sr: "",
      indx: 0,
      bur: 0,
    };
  },
  methods: {
    mouse(e) {
      let y = e.target,
        x = e.target;
      x = e.clientX;
      y = e.clientY;
      this.$refs.cursor.style.top = `${y + this.sr - 10}px`;
      this.$refs.cursor.style.left = `${x - 10}px`;
    },
    txtCreate() {
      const h1 = "Hi,my name is Aleksandr.\nI'm a frontend developer.";
      for (let i = 0; i < h1.length; i++) {
        setTimeout(() => {
          this.h1 += h1[i];
        }, 50 * i);
      }
    },
    scrollm() {
      let scr = 0;
      document.addEventListener("scroll", () => {
        scr = document.innerHTML = scrollY;
        this.sr = scr;
      });
    },
    observed() {
      function onEntry(entry) {
        entry.forEach((change) => {
          if (change.isIntersecting) {
            change.target.classList.add("active");
          } else {
            change.target.classList.remove("active");
          }
        });
        console.log(elements);
      }

      let options = {
        threshold: [0.1],
      };
      let observer = new IntersectionObserver(onEntry, options);
      let elements = document.querySelectorAll(".anim-items");

      for (let elm of elements) {
        observer.observe(elm);
      }
    },
    sliderNext() {
      const wdth = window.innerWidth;
      console.log(this.indx);
      if (this.indx < 6) {
        this.indx++;
        this.$refs.line.style.left = `-${wdth * this.indx}px`;
      } else {
        this.indx = 0;
        this.$refs.line.style.left = `-${wdth * this.indx}px`;
      }
    },
    sliderPrev() {
      const wdth = window.innerWidth;
      this.indx--;
      if (this.indx < 0) {
        this.indx = 6;
        this.$refs.line.style.left = `-${wdth * this.indx}px`;
      } else {
        this.$refs.line.style.left = `-${wdth * this.indx}px`;
      }
    },
    burgerAdd() {
      this.bur++;
      if (this.bur == 1) {
        this.$refs.arrow1.style.width = "0";
        this.$refs.burger.classList.add("active");
        setTimeout(() => {
          this.$refs.arrow.style.transform =
            "rotateZ(45deg) translate(1px ,-1px)";
          this.$refs.arrow2.style.transform = "rotateZ(-45deg) ";
        }, 200);
      } else {
        this.bur = 0;
        this.$refs.arrow.style.transform = "rotateZ(0deg)translate(0 ,0)";
        this.$refs.arrow2.style.transform = "rotateZ(0deg)";
        this.$refs.burger.classList.remove("active");
        setTimeout(() => {
          this.$refs.arrow1.style.width = "20px";
        }, 300);
      }
    },
    burgerDel() {
      this.bur = 0;
      if (this.bur == 0) {
        this.$refs.burger.classList.remove("active");
        this.$refs.arrow.style.transform = "rotateZ(0deg)translate(0 ,0)";
        this.$refs.arrow2.style.transform = "rotateZ(0deg)";
        setTimeout(() => {
          this.$refs.arrow1.style.width = "20px";
        }, 300);
      }
    },
  },
  created: function () {
    this.txtCreate();
    this.scrollm();
  },
  mounted: function () {
    this.observed();
  },
};
</script>

